






import { Component, Vue, Prop } from 'vue-property-decorator';
import { State, Mutation, namespace } from 'vuex-class'
// @ts-ignore
import { Sketch } from 'vue-color'
const ModuleState = namespace('board', State)
const ModuleMutation = namespace('board', Mutation)

@Component({
  components: { 'color-picker': Sketch }
})
export default class IColorSetting extends Vue {
  colors = 'black'
  @ModuleMutation setColor!: Function
  changeColor(colors: any) {
    const { r, g, b, a } = colors.rgba
    this.setColor(`rgba(${r},${g},${b},${a})`)
  }
}



