import { State } from '@/type/history'

const stepLength = 10

const state: State = {
  indexR: 0,
  recordList: [],
  lock: false
}

const getters = {
  enableUndo(state: State) {
    if (!state.recordList.length) return false
    return state.indexR < state.recordList.length - 1
  },
  enableRedo(state: State) {
    if (!state.recordList.length || !state.indexR) return false
    return state.indexR !== 0
  }
}

const mutations = {
  updateIndexR(state: State, index: number) {
    state.indexR = index
  },
  record(state: State, project: any) {
    if (state.lock) return
    const newRecordList = state.recordList.slice(0, stepLength - 1 - state.indexR)
    newRecordList.push(project)
    state.indexR = 0
    if (newRecordList.length > stepLength) newRecordList.shift()
    state.recordList = newRecordList
  },
  undo(state: State) {
    if (state.indexR === stepLength - 1 || state.indexR === state.recordList.length - 1) return
    state.indexR++
  },
  redo(state: State) {
    if (state.indexR === 0) return
    state.indexR--
  },
  lockHistory(state: State) {
    state.lock = true
  },
  unlockHistory(state: State) {
    state.lock = false
  }
}

const subscribe = function ({ type, payload }: { type: string, payload: any }, { history }: { history: State }) {
  if (type === 'history/record') {
    // Base64localStorage
    // localStorage.setItem('history', JSON.stringify(history))
  }
}

// if (localStorage.history)
//   Object.assign(state, JSON.parse(localStorage.history))

export default {
  name: 'history',
  namespaced: true,
  getters,
  state,
  mutations,
  subscribe
}


