<template>
  <v-layout row justify-center>
    <v-dialog v-model="show" persistent max-width="290">
      <v-card color="white">
        <v-card-title v-if="title" class="headline">{{title}}</v-card-title>
        <v-card-text v-if="content">{{content}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue" flat @click="show=false"></v-btn>
          <v-btn color="blue" flat @click="show=false" @click.native="$confirm"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>
<script>
  export default {
    name: 'GlobalConfirm',
    data: _ => ({ show: false, title: '', content: '' }),
    methods: {
      open(options) {
        let title = '',
          content = ''
        if (options instanceof Object) {
          if (options.title) title = ''
          content = options.content
        } else content = options

        this.title = title
        this.content = content
        this.show = true
        return new Promise(resolve => (this.$confirm = resolve))
      }
    }
  }
</script>


