<template>
  <i-tip-button :button="button" :highlight="selectMode === button.name" v-if="!button.type" />
  <v-menu v-else-if="['color','size'].includes(button.type)" offset-y nudge-top="-20" :close-on-content-click="false">
    <template v-if="button.type==='color'">
      <i-tip-button :button="button" slot="activator" :color="color" />
      <IColorSetting v-if="button.type==='color'" />
    </template>
    <template v-else>
      <i-tip-button :button="button" slot="activator" />
      <IBrushSetting v-if="button.type==='size'" />
    </template>
  </v-menu>
</template>

<script>
  import ITipButton from './tipButton.vue'
  import { mapState, mapMutations } from 'vuex'
  import IColorSetting from '../colorSetting.vue'
  import IBrushSetting from '../brushSetting.vue'

  export default {
    name: 'IIconButton',
    components: {
      ITipButton,
      IColorSetting,
      IBrushSetting
    },
    props: {
      button: {
        type: Object,
        default: {
          name: String,
          tip: String,
          icon: String,
          type: String
        }
      },
      highlight: Boolean
    },
    computed: {
      ...mapState('board', ['selectMode', 'color'])
    }
  }
</script>


