export const DEVICE_WIDTH = window ? document.documentElement.clientWidth : null
export const DEVICE_MOBILE =
  !DEVICE_WIDTH ? null :
    DEVICE_WIDTH < 960 ? true : false
// Reference https://vuetifyjs.com/en/layout/display#visibility
export const DEVICE_VIEWPORT =
  !DEVICE_WIDTH ? null :
    DEVICE_WIDTH < 600 ? 'xs' :
      DEVICE_WIDTH < 960 ? 'sm' :
        DEVICE_WIDTH < 1264 ? 'md' :
          DEVICE_WIDTH < 1940 ? 'lg' : 'xl'
export const SYSTEM_IOS = window ? navigator.userAgent.toLowerCase().indexOf('iphone') !== -1 : false

export const DIALOG_PREVIEW = 'Dialog preview'


