import Vue from 'vue'
import { clone } from 'ramda'

Vue.filter('style', function (style: { [key: string]: string | number }) {
  style = clone(style)
  const px = ['width', 'height', 'fontSize', 'lineHeight', 'left', 'top', 'right', 'bottom', 'padding', 'bottom']
  Object.keys(style).forEach(key => {
    if (px.includes(key))
      style[key] += 'px'
  })
  return style
})


