












import { Component, Vue, Prop } from 'vue-property-decorator';
import { ResponseResult } from '@/type/draw'
import { State, Mutation, namespace } from 'vuex-class'
const ModuleState = namespace('board', State)
const ModuleMutation = namespace('board', Mutation)

@Component
export default class IDrawTextSetting extends Vue {
  @Prop({ default: '100%' }) width!: string
  @ModuleState fontSize!: string
  @ModuleMutation setFontSize!: Function
  get allSize() {
    // @ts-ignore
    return Array.apply(null, { length: 30 }).map((_, i) => ({ text: i + 12 }))
  }
}



