<template>
  <v-dialog v-model="show" :max-width="DEVICE_MOBILE?'90%':'50%'">
    <v-card>
      <v-card-title>
        <span class="headline"></span>
        <v-spacer></v-spacer>
        <v-btn icon @click="show=false">
          <v-icon>close</v-icon>
        </v-btn>
      </v-card-title>
      <img :src="url" width="100%`" height="auto" align="center" />
    </v-card>
  </v-dialog>
</template>

<script>
  import { DIALOG_PREVIEW } from '@/lib/constant'
  export default {
    name: 'IPreview',
    data: _ => ({ url: '', show: false }),
    events: {
      [DIALOG_PREVIEW](url) {
        this.url = url
        this.show = true
      }
    }
  }
</script>


