<template>
  <v-layout row wrap>
    <v-flex xs6>
      <IIconButton class="icon-button" v-for="(item,$index) in toolList" :key="$index" :button="item" @click.native="changeSelectMode(item.name)"
      />
    </v-flex>
    <v-flex xs6>
      <IIconButton class="icon-button" v-for="(item,$index) in toolListRight" :key="$index" :button="item"
        @click.native="item.event && item.event()" />
    </v-flex>
  </v-layout>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  import ILayerList from './layerList.vue'
  import {
    MODE_NEW_PROJECT,
    MODE_SELECT,
    MODE_BRUSH,
    MODE_ERASER,
    MODE_FILL,
    MODE_ADD_TEXT,
    MODE_COLORIZE,
    MODE_CHANGE_COLOR,
    EV_COLORIZE,
    MODE_DOWNLOAD,
    EV_DOWNLOAD_CANVAS,
    MODE_EMPTY,
    MODE_BRUSH_SIZE,
    MODE_ADD_IMAGE,
    EV_EMPTY_PROJECT,
    EV_ADD_IMAGE
  } from '@/lib/constant'
  import IIconButton from './iconButton/index.vue'

  export default {
    name: 'IToolbar',
    components: { ILayerList, IIconButton },
    data() {
      const toolList = [
        {
          name: MODE_SELECT,
          icon: 'iconfont icon-pointer',
          tip: ''
        },
        {
          name: MODE_BRUSH,
          icon: 'mode_edit',
          tip: ''
        },
        {
          name: MODE_ERASER,
          icon: 'iconfont icon-mianxingtubiao_xiangpica',
          tip: ''
        },
        {
          name: MODE_FILL,
          icon: 'format_color_fill',
          tip: ''
        },
        {
          name: MODE_ADD_TEXT,
          icon: 'title',
          tip: ''
        }
      ]
      const toolListRight = [
        {
          name: MODE_CHANGE_COLOR,
          icon: 'color_lens',
          tip: '',
          type: 'color'
        },
        {
          name: MODE_BRUSH_SIZE,
          icon: 'gesture',
          tip: '',
          type: 'size'
        },
        {
          name: MODE_COLORIZE,
          icon: 'assistant',
          tip: '',
          event: () => this.$events.emit(EV_COLORIZE)
        },
        {
          name: MODE_ADD_IMAGE,
          icon: 'image',
          tip: '',
          event: () => this.$events.emit(EV_ADD_IMAGE)
        },
        {
          name: MODE_DOWNLOAD,
          icon: 'file_download',
          tip: '',
          event: () => this.$events.emit(EV_DOWNLOAD_CANVAS)
        },
        {
          name: MODE_EMPTY,
          icon: 'delete_forever',
          tip: '',
          event: () => this.$events.emit(EV_EMPTY_PROJECT)
        }
      ]
      return {
        toolList,
        toolListRight
      }
    },
    methods: {
      ...mapMutations('board', ['changeSelectMode'])
    },
    computed: {
      ...mapState('board', ['selectMode', 'layers'])
    }
  }
</script>
<style lang="less" scoped>
  .icon-button {
    display: block;
  }
</style>



