<template>
  <section class="container" :style="{height}">
    <div class="opration-list">
      <IToolbar/>
    </div>
    <div class="do-history">
      <IDoHistory/>
    </div>
    <div class="layer-list">
      <ILayerList/>
    </div>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import ILayerList from './layerList.vue'
  import IToolbar from './toolbar.vue'
  import IDoHistory from './doHistory.vue'
  import { DEVICE_MOBILE } from '@/lib/constant'
  export default {
    name: 'ISidebar',
    components: { ILayerList, IToolbar, IDoHistory },
    computed: {
      ...mapState('board', ['canvasSize']),
      height() {
        return DEVICE_MOBILE ? '100%' : this.canvasSize.height + 'px'
      }
    }
  }
</script>
<style lang="less" scoped>
  .container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 0;
    .opration-list,
    .layer-list {
      width: 100%;
    }
    .opration-list {
      overflow-y: scroll;
      height: 50%;
    }
    .layer-list {
      height: 45%;
      box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2),
        0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    }
    .do-history {
      height: 50px;
      margin: 10px 0;
    }
  }
</style>


