<template>
  <v-navigation-drawer width="120" :disable-resize-watcher="true" clipped touchless v-model="open" app
    class="drawer">
    <ISidebar/>
  </v-navigation-drawer>
</template>

<script>
  import { EV_TOGGLE_DRAWER } from '@/lib/constant'
  import ISidebar from '@/components/sidebar/index.vue'
  export default {
    name: 'IDrawer',
    components: { ISidebar },
    data: () => ({ open: false }),
    events: {
      [EV_TOGGLE_DRAWER]() {
        this.open = !this.open
      }
    }
  }
</script>

<style lang="less">
  .drawer {
    padding: 0;
    transition: 0.05s cubic-bezier(0.4, 0, 0.2, 1) !important; //Rewrite vuetify transition.
    opacity: 0.92;
  }
</style>



