import confirm from './index.vue'

const plugin = {
  install(Vue) {
    const Component = Vue.extend(confirm)
    const confirmVm = new Component().$mount()
    document.body.appendChild(confirmVm.$el)
    Vue.prototype.$confirm = confirmVm.open
  }
}
export default plugin


