<template>
  <v-content>
    <v-container fluid>
      <v-layout row wrap>
        <v-flex xs12 sm12 md9 lg10 xl11>
          <v-card>
            <IDraw/>
          </v-card>
        </v-flex>
        <v-flex xs0 sm0 md3 lg2 xl1 class="side-bar">
          <ISidebar v-if="!DEVICE_MOBILE" />
        </v-flex>
      </v-layout>
    </v-container>
    <IPreview />
  </v-content>
</template>

<script>
  import IDraw from '@/components/draw/index.vue'
  import ISidebar from '@/components/sidebar/index.vue'
  import IPreview from '@/components/dialog/preview.vue'

  export default {
    name: 'IBoard',
    components: { IDraw, ISidebar, IPreview }
  }
</script>

<style scoped>
  .side-bar {
    padding-left: 20px;
  }
</style>


