<template>
  <section>
    <ITipButton :class="{show:enableUndo}" :button="{tip:'',icon:'undo'}" @click.native="undo" />
    <ITipButton :class="{show:enableRedo}" :button="{tip:'',icon:'redo'}" @click.native="redo" />
  </section>
</template>

<script>
  import ITipButton from './iconButton/tipButton.vue'
  import { mapGetters, mapMutations } from 'vuex'
  export default {
    name: 'IDoHistory',
    components: { ITipButton },
    computed: {
      ...mapGetters('history', ['enableUndo', 'enableRedo']),
      history() {
        const history = localStorage.history
        return history
      }
    },
    methods: {
      ...mapMutations('history', ['undo', 'redo'])
    }
  }
</script>

<style scoped lang="less">
  section {
    & > * {
      visibility: hidden;
      pointer-events: none;
      width: 50%;
      display: inline-block;
      &.show {
        visibility: initial;
        pointer-events: initial;
      }
    }
  }
</style>


