<template>
  <div class="menu-container" :style="style.container" v-if="style.container.display!=='none'">
    <ul class="menu">
      <li class="menu-item" v-for="(item,$index) in menu" :key="$index" @click="click(item,$event)">{{item.title}}</li>
    </ul>
  </div>
</template>

<script>
  import { clamp } from 'ramda'
  export default {
    name: 'GlobalMenu',
    data() {
      return {
        style: {
          container: {
            display: 'none'
          }
        },
        temp: {
          left: null,
          top: null,
          event: null
        }
      }
    },
    methods: {
      show(left, top, event) {
        this.temp = { left, top, event }
        this.style.container = {}
        this.$nextTick(() => {
          const { clientWidth, clientHeight } = document.documentElement
          left = clamp(0, clientWidth - this.$root.$el.clientWidth - 10, left)
          top = clamp(0, clientHeight - this.$root.$el.clientHeight - 10, top)
          this.style.container = { left: left + 'px', top: top + 'px' }
        })
      },
      close() {
        this.style.container = { display: 'none' }
      },
      click(item, event) {
        if (item.event)
          item.event({ item, openEvent: this.temp.event, selectEvent: event })
        this.close()
      }
    }
  }
</script>

<style lang="less" scoped>
  @import 'colors';
  .menu-container {
    display: inline-block;
    position: fixed;
    padding: 10px 0;
    min-width: 100px;
    background: white;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.117647),
      0 1px 4px rgba(0, 0, 0, 0.117647);
    z-index: 999;
    border-radius: 3px;
    color: @grey800;
    user-select: none;
    ul {
      list-style-type: none;
      display: block;
      li {
        line-height: 2.5em;
        padding: 0 10px;
        cursor: pointer;
        &:hover {
          background: @grey100;
        }
      }
    }
  }
</style>


