import loading from './index.vue'

const plugin = {
  install(Vue) {
    const Component = Vue.extend(loading)
    const loadingVm = new Component().$mount()
    document.body.appendChild(loadingVm.$el)
    Vue.prototype.$loading = {
      open: loadingVm.open,
      close: loadingVm.close
    }
  }
}
export default plugin


