import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const files = require.context('./modules/', false, /\.ts|\.js$/)
const modules: { [key: string]: any } = {}

files.keys().forEach(key => {
  const module = files(key).default
  const addModule = (name: string) => Object.defineProperty(modules, name, { value: module, enumerable: true })
  if (module.name) {
    addModule(module.name)
  } else {
    addModule(key.replace(/(\.\/|\.js|\.ts)/g, ''))
  }
})

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  modules
})

export default store

for (let key in modules) {
  const subscribe = modules[key].subscribe
  if (subscribe) store.subscribe(subscribe)
}



