






















import { Component, Vue, Watch } from 'vue-property-decorator';
import { Draw } from '@/lib/draw'
import { Layer } from '@/type/draw'
import { State, Mutation, Action, namespace } from 'vuex-class'
import { EV_DELETE_LAYER, EV_COPY_LAYER, EV_MERGE_LAYER, EV_UPDATE_LAYER_DEPTH } from '@/lib/constant'
import IEditable from '@/components/global/editable.vue'
// @ts-ignore
import draggable from 'vuedraggable'
const ModuleMutation = namespace('board', Mutation)
const ModuleState = namespace('board', State)

@Component({
  computed: {
    layers: {
      get() {
        return this.$store.state.board.layers.slice().reverse()
      },
      set(value) {
        // this event will trigger 'onLayersChange' , layerList will be change.
        this.$events.emit(EV_UPDATE_LAYER_DEPTH, value)
      }
    }
  },
  components: { IEditable, draggable }
})
export default class ILayerList extends Vue {
  @ModuleMutation selectLayer!: Function
  selectedId!: number
  get iconMenu() {
    const menu = {
      triggerEvent: ['click'],
      menu: [
        {
          title: '',
          event: () => this.$events.emit(EV_COPY_LAYER)
        },
        {
          title: '',
          event: () => this.mergeLayer()
        },
        {
          title: '',
          event: () => this.$events.emit(EV_DELETE_LAYER, this.selectedId)
        }
      ]
    }
    return menu
  }
  mergeLayer() {
    const fromId = this.selectedId
    const layers = (<any>this).layers as Layer[]
    const toIndex = layers.findIndex(l => l.id === fromId) + 1
    const toId = layers[toIndex].id
    console.log(fromId, toId, toIndex)
    if (toIndex >= layers.length) return
    this.$events.emit(EV_MERGE_LAYER, { fromId, toId })
  }
}



