









import { Component, Vue, Watch } from 'vue-property-decorator';
import { Draw } from '@/lib/draw'
import { ResponseResult, CanvasSize, Layer } from '@/type/draw'
import { State, Mutation, Action, Getter, namespace } from 'vuex-class'
import {
  MODE_NEW_PROJECT,
  MODE_SELECT,
  MODE_BRUSH,
  MODE_FILL,
  MODE_ERASER,
  MODE_ADD_TEXT,
  MODE_COLORIZE,
  MODE_EMPTY,
  EV_ADD_SVG_TO_LAYER,
  EV_COLORIZE,
  EV_ADD_LAYER,
  EV_CHANGE_LAYER_NAME,
  EV_DELETE_LAYER,
  EV_COPY_LAYER,
  EV_MERGE_LAYER,
  EV_UPDATE_LAYER_DEPTH,
  DIALOG_PREVIEW,
  EV_DOWNLOAD_CANVAS,
  EV_EMPTY_PROJECT,
  EV_ADD_IMAGE,
  SYSTEM_IOS
} from "@/lib/constant"
import { Event, MouseEvent } from 'paper';
import { PointText } from 'paper'
import { Record } from '@/type/history'
import { Debounce } from 'lodash-decorators'
const boardMutation = namespace('board', Mutation)
const boardState = namespace('board', State)
const boardAction = namespace('board', Action)
const boardGetter = namespace('board', Getter)
const historyMutation = namespace('history', Mutation)
const historyState = namespace('history', State)
const defaultInputStyle: { [key: string]: string | number } = {
  display: 'none',
  left: '-100',
  top: '-100',
  color: 'initial'  }

@Component
export default class IDraw extends Vue {
  @boardState selectedResult!: ResponseResult
  @boardState canvasSize!: CanvasSize
  @boardState selectMode!: string
  @boardState color!: string
  @boardState fontSize!: number
  @boardState brushSize!: number
  @boardGetter activeLayer!: Layer
  @boardMutation setResults!: Function
  @boardMutation removeResultItem!: Function
  @boardMutation setCanvasSize!: Function
  @boardMutation setLayers!: Function
  @boardMutation selectLayer!: Function
  @historyState indexR!: number
  @historyState recordList!: any[]
  @historyMutation record!: Record
  @historyMutation lockHistory!: Function
  @historyMutation unlockHistory!: Function
  draw: Draw = {} as Draw
  inputStyle = defaultInputStyle
  mounted() {
    const { width, height } = this.resize()
    this.draw = new Draw(this.$refs.canvas as HTMLCanvasElement, width, height)
    this.draw.onResponse = (res: ResponseResult[]) => this.setResults(res)
    this.addLayer()
    this.setCanvasSize({ width, height })

    this.$events.on(EV_ADD_SVG_TO_LAYER, ({ url }) => this.draw.drawSVG(url, true))
    this.$events.on(EV_COLORIZE, () => this.colorize())
    this.$events.on(EV_ADD_LAYER, () => this.addLayer())
    this.$events.on(EV_CHANGE_LAYER_NAME, name => this.draw.changeLayerName(name))
    this.$events.on(EV_DELETE_LAYER, id => this.draw.deleteLayer(id))
    this.$events.on(EV_COPY_LAYER, id => this.draw.copyLayer(id))
    this.$events.on(EV_MERGE_LAYER, arg => this.draw.mergeLayer(arg))
    this.$events.on(EV_UPDATE_LAYER_DEPTH, (layers: Layer[]) => this.updateLayerDepth(layers))
    this.$events.on(EV_DOWNLOAD_CANVAS, () => this.download())
    this.$events.on(EV_EMPTY_PROJECT, () => this.emptyCanvas())
    this.$events.on(EV_ADD_IMAGE, () => this.addImage())
  }
  resize(): CanvasSize {
    const { width, top } = (this.$el as Element).getBoundingClientRect()
    const height = document.documentElement.clientHeight - top - 20
    this.setCanvasSize({ width, height })
    return { width, height }
  }
  async addLayer() {
    const layer = this.draw.createLayer()
    // waiting for layer add to layerList
    await new Promise(resolve => setTimeout(resolve, 0))
    this.selectLayer(layer)
  }
  @Watch('selectedResult', { deep: true })
  async onSelecteResultChange({ url }: ResponseResult) {
    this.draw.drawSVG(url)
  }
  @Watch('selectMode')
  async onSelectModeChange(mode: string) {
    this.draw.setSelectMode(mode)
    //waiting for the layer border to be removed
    await new Promise(resolve => setTimeout(resolve, 0))
    if (mode !== MODE_ADD_TEXT) this.updateText()
  }
  @Watch('draw.project.layers')
  onLayersChange(layers: Array<paper.Layer>) {
    const active = this.draw.project.activeLayer
    const allLayer = layers.map(layer => {
      const iLayer = {
        id: layer.id,
        name: layer.name,
        selected: layer.id === active.id
      }
      return iLayer
    })
    this.setLayers(allLayer)
  }
  @Watch('draw.shapes')
  @Debounce(1000)
  onShapeChange() {
    this.record(this.draw.project.exportJSON({ asString: false }))
  }
  @Watch('activeLayer')
  onActiveLayerChange(layer: Layer) {
    if (layer)
      this.draw.selectLayer(layer.id)
  }
  @Watch('color')
  onColorChange(color: string) {
    this.draw.color = color
    this.inputStyle = Object.assign({}, this.inputStyle, { color })
  }
  @Watch('fontSize')
  onFontSizeChange(fontSize: number) {
    this.draw.fontSize = fontSize
    this.inputStyle = Object.assign({}, this.inputStyle, { fontSize, lineHeight: fontSize })
  }
  @Watch('brushSize')
  onBrushSizeChange(brushSize: number) {
    this.draw.strokeWidth = brushSize
  }
  @Watch('indexR')
  doHistory(indexR: number) {
    this.lockHistory()
    this.draw.project.clear()
    const index = this.recordList.length - 1 - indexR
    const project = this.recordList[index]
    this.draw.project.importJSON(JSON.stringify(project))
    this.draw.updateBorder()
    this.unlockHistory()
  }
  get style() {
    const { width, height } = this.canvasSize
    const mode = this.selectMode
    const cursor =
      mode === MODE_SELECT ? 'move' :
        mode === MODE_BRUSH ? 'crosshair' :
          mode === MODE_FILL ? 'crosshair' :
            mode === MODE_ERASER ? 'crosshair' :
              mode === MODE_ADD_TEXT ? 'text' :
                mode === MODE_COLORIZE ? 'cell' : null
    return {
      width: width + 'px',
      height: this.canvasSize.height + 'px',
      cursor,
      display: 'block'
    }
  }
  get menu() {
    const options = {
      triggerEvent: ['contextmenu'],
      menu: [{
        title: '',
        event: this.selectLayerByCoord
      }, {
        title: '',
        event: () => this.$events.emit(EV_COPY_LAYER)
      }]
    }
    return options
  }
  get relativeCoord() {
    return (e: NativeMouseEvent | TouchEvent): [number, number] => {
      const { left, top } = (this.$refs.canvas as HTMLCanvasElement).getBoundingClientRect()
      let coord = { x: 0, y: 0 }
      if (e instanceof TouchEvent)
        coord = {
          x: e.targetTouches[0].pageX,
          y: e.targetTouches[0].pageY
        }
      else coord = {
        x: e.x,
        y: e.y
      }
      const { x, y } = coord
      return [x - left, y - top]
    }
  }
  selectLayerByCoord({ openEvent }: any) {
    const [x, y] = this.relativeCoord(openEvent)
    const layer = this.draw.getLayerByCoord(x, y)
    if (layer) this.selectLayer(layer.id)
  }
  async updateText(e?: NativeMouseEvent | TouchEvent) {
    const offset = 10
    const edit = this.$refs.edit as HTMLTextAreaElement
    const bounds = this.$el.getBoundingClientRect()
    if (e && this.selectMode === MODE_ADD_TEXT) {
      let coord = { x: 0, y: 0 }
      if (e instanceof TouchEvent)
        coord = {
          x: e.targetTouches[0].pageX,
          y: e.targetTouches[0].pageY
        }
      else coord = {
        x: e.x,
        y: e.y
      }
      this.inputStyle = {
        display: 'initial',
        left: (coord.x - bounds.left - offset),
        top: (coord.y - bounds.top - offset),
        fontSize: this.fontSize,
        lineHeight: this.fontSize,
        color: this.color
      }
      await new Promise(resolve => setTimeout(resolve, 0))
      edit.focus()
      // Create Text item  Update Text item
      const [x, y] = this.relativeCoord(e)
      const layer = this.draw.getLayerByCoord(x, y)
      if (!layer) return
      const item = layer.children.content.getItem((item: any) => item instanceof PointText) as paper.PointText
      if (!item) return
      const { width, height, left, top } = item.bounds
      this.inputStyle.left = left
      this.inputStyle.top = top
      this.inputStyle.width = width
      this.inputStyle.height = height
      item.visible = false
      edit.value = item.content
      edit.setAttribute('data-type', 'update')
      edit.setAttribute('data-item-id', item.id.toString())
    } else {
      this.inputStyle = defaultInputStyle
      const text = edit.value
      edit.value = ''
      if (!text) return
      const isUpdate = edit.getAttribute('data-type') === 'update'
      const textItemId = edit.getAttribute('data-item-id') as string
      edit.setAttribute('data-type', '')
      edit.setAttribute('data-item-id', '')
      if (isUpdate) {
        const findItem = (l: paper.Layer) => l.children.content.getItem((i: paper.Item) => i.id === +textItemId)
        const layer = this.draw.project.getItem(findItem) as paper.Layer
        if (!layer) return
        const item = findItem(layer) as paper.PointText
        if (!item) return
        item.content = text
        item.fontSize = this.fontSize
        item.visible = true
        item.fillColor = this.color
        this.draw.selectLayer(layer.id)
        this.draw.updateBorder()
      } else {
        const { left, top } = edit.getBoundingClientRect()
        this.draw.addText(text, { left: left - bounds.left + offset, top: top - bounds.top + offset })
      }
    }
  }
  colorize() {
    this.$loading.open()
    this.draw.colorize().then(async url => {
      const img = new Image()
      img.src = url
      await new Promise(resolve => img.onload = resolve)
      this.$loading.close()
      if (url) this.$events.emit(DIALOG_PREVIEW, url)
    }).catch(err => {
      this.$loading.close()
    })
  }
  updateLayerDepth(layers: Layer[]) {
    layers.forEach(({ id }) => {
      const layer = this.draw.project.layers.find(l => l.id === id)
      if (layer) layer.sendToBack()
    })
  }
  async download() {
    const canvas = (<HTMLCanvasElement>this.$refs.canvas)
    const blob = await new Promise(resolve => canvas.toBlob(resolve as any))
    const url = URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.setAttribute('href', url)
    link.setAttribute('download', Date.now() + '.gif')
    link.click()
  }
  emptyCanvas() {
    this.$confirm('').then(() => {
      this.draw.clearShapes()
      this.draw.project.clear()
      this.addLayer()
    })
  }
  async addImage() {
    const input = document.createElement('input')
    input.type = 'file'
    input.accept = 'image/png, image/jpeg, image/jpg'
    input.click()
    await new Promise(resolve => input.onchange = resolve)
    const imageFile = (<FileList>input.files)[0]
    const reader = new FileReader
    reader.readAsDataURL(imageFile)
    const result = await new Promise(resolve => reader.onloadend = resolve)
    const base64 = reader.result
    const image = document.createElement('img')
    image.src = base64
    image.onload = _ => this.draw.addImage(image)
  }
  lastRotation = { rotation: 0, scale: 0 }
  rotate(e: any) {
    const isStart = this.lastRotation.rotation === 0 && this.lastRotation.scale === 0
    const { rotation, scale } = e
    const deltaRotation = rotation - this.lastRotation.rotation
    const deltaScale = scale - this.lastRotation.scale
    this.lastRotation = { rotation, scale }
    if (this.selectMode !== MODE_SELECT) return
    if (isStart) return
    const layer = this.draw.project.activeLayer
    if (!layer) return
    layer.rotate(deltaRotation)
    // layer.scale(scale)
  }
  rotateend() {
    this.lastRotation = { rotation: 0, scale: 0 }
  }
}



