<template>
  <div class="loading-container" v-if="show">
    <v-progress-circular indeterminate color="primary"></v-progress-circular>
  </div>
</template>

<script>
  export default {
    name: 'GlobalLoading',
    data: _ => ({ show: false }),
    methods: {
      open() {
        this.show = true
      },
      close() {
        this.show = false
      }
    }
  }
</script>

<style scoped>
  .loading-container {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
  }
</style>


