import { State, Axis, CurrentShape, ResponseResult, CanvasSize, Layer } from '@/type/draw'
import {
  MODE_SELECT,
  MODE_BRUSH,
  MODE_FILL,
  MODE_ADD_TEXT, MODE_COLORIZE, MODE_CHANGE_COLOR
} from '@/lib/constant'
import { ActionContext, Action } from 'vuex';

type LayerId = number

export const defaultFontSize = 16
export const defaultBrushSize = 5
export const defaultColor = 'black'

const state: State = {
  results: [],
  selectedResult: null,
  canvasSize: {
    width: 0,
    height: 0
  },
  selectMode: MODE_BRUSH,
  layers: [],
  color: defaultColor,
  fontSize: defaultFontSize,
  brushSize: defaultBrushSize
}

const getters = {
  activeLayer(state: State) {
    return state.layers.find(l => l.selected)
  }
}

export const mutations = {
  setResults(state: State, results: Array<ResponseResult>) {
    state.results = results
  },
  removeResultItem(state: State, index: number) {
    state.results.splice(index, 1)
  },
  selectResult(state: State, result: ResponseResult) {
    // trigger watch event
    state.selectedResult = null
    state.selectedResult = result
  },
  setCanvasSize(state: State, size: CanvasSize) {
    state.canvasSize = size
  },
  changeSelectMode(state: State, mode: string) {
    state.selectMode = mode
  },
  updateLayer(state: State, attr: { [key: string]: any, id: number }) {
    const index = state.layers.findIndex(layer => layer.id === attr.id)
    const newLayer = Object.assign({}, state.layers[index], attr)
    state.layers.splice(index, 1, newLayer)
  },
  setLayers(state: State, layers: Layer[]) {
    state.layers = layers
  },
  selectLayer(state: State, _layer: Layer | LayerId) {
    if (_layer === undefined) return
    const layer = (typeof _layer === 'number' ? state.layers.find(l => l.id === _layer) : _layer) as Layer
    state.layers.forEach(l => l.selected = false)
    const index = state.layers.findIndex(l => l.id === layer.id)
    const newLayer = state.layers.splice(index, 1)[0]
    newLayer.selected = true
    state.layers.splice(index, 0, newLayer)
  },
  setColor(state: State, color: string) {
    state.color = color
  },
  setFontSize(state: State, size: number) {
    state.fontSize = size
  },
  setBrushSize(state: State, size: number) {
    state.brushSize = size
  }
}

export default {
  name: 'board',
  namespaced: true,
  getters,
  state,
  mutations,
}



