import menu from './index.vue'
import { get } from 'lodash'

const plugin = {
  install(Vue) {
    Vue.directive('menu', {
      bind(el, { value }, { context }) {
        const Component = Vue.extend({
          ...menu,
          data() {
            return {
              ...menu.data(),
              menu: value.menu
            }
          }
        })
        const menuVm = new Component().$mount()
        context.$data.$menuVm = menuVm
        document.body.appendChild(menuVm.$el)
        //open menu
        const event = event => plugin.contextmenu({ Vue, el, value, event, menuVm })
        value.triggerEvent.forEach(name => el.addEventListener(name, event))
        //close menu
        document.addEventListener('mousedown', e => {
          if (~e.target.className.indexOf('menu')) return
          menuVm.close()
        })
      },
      unbind(e, binding, { context }) {
        const el = get('context', '$data.$menuVm,$el')
        const parent = get(el, 'parentElement')
        parent && parnet.removeChild(el)
      }
    })
  },
  contextmenu({ event, menuVm }) {
    event.preventDefault()
    const { pageX, pageY } = event
    menuVm.show(pageX, pageY, event)
  }
}
export default plugin


