








import { Component, Vue, Prop } from 'vue-property-decorator';
import { ResponseResult } from '@/type/draw'
import { State, Mutation, namespace } from 'vuex-class'
import { EV_ADD_SVG_TO_LAYER } from '@/lib/constant'
const ModuleState = namespace('board', State)
const ModuleMutation = namespace('board', Mutation)

@Component
export default class IDrawResult extends Vue {
  @ModuleState results!: ResponseResult[]
  @ModuleMutation removeResultItem!: Function
  @ModuleMutation selectResult!: Function
  selectedResult!: ResponseResult
  @Prop({ default: '100%' }) width!: string
  get contextmenu() {
    const options = {
      triggerEvent: ['contextmenu'],
      menu: [{
        title: '',
        event: this.addSVGToNewLayer
      }]
    }
    return options
  }
  menu(item: ResponseResult) {
    this.selectedResult = item
  }
  addSVGToNewLayer() {
    const svg = this.selectedResult
    this.$events.emit(EV_ADD_SVG_TO_LAYER, svg)
  }
}



