// @flow
import axios from 'axios'
import { Axis, CurrentShape, ResponseResult, CanvasSize } from '@/type/draw'
import { defaultBrushSize, defaultColor, defaultFontSize } from '@/store/modules/board'
import {
  MODE_NEW_PROJECT,
  MODE_SELECT,
  MODE_BRUSH,
  MODE_ERASER,
  MODE_FILL,
  MODE_ADD_TEXT, MODE_COLORIZE,
  DEVICE_MOBILE
} from "@/lib/constant";
import { flatten, union, inRange, noop, get, last } from 'lodash'
import { Debounce } from 'lodash-decorators'
import { clone } from 'ramda'
import { Path, Point, Project, Raster, Layer, Size, PointText, Shape, Group, Rectangle, CompoundPath, Symbol } from 'paper'
import { setTimeout } from 'timers';

declare module 'paper' {
  interface Layer {
    children: paper.Item[] & {
      content: Group,
      border: Group & {
        children: paper.Item[] & {
          dashed: Group
        }
      }
    }
  }
}

const RIGHT_CLICK_NUM = 2

export class Draw {
  private el: HTMLCanvasElement
  private ctx: CanvasRenderingContext2D
  private drawing: boolean = false
  private previousAxis: paper.Point = new Point(0, 0)
  private pressedAt?: number
  private currentShape: CurrentShape = []
  private shapes: Array<CurrentShape> = []
  private rect!: ClientRect | DOMRect
  private path!: paper.Path
  private selectMode: string = MODE_BRUSH
  private areaBorder!: Group
  private borderPadding = 0
  private texture!: HTMLImageElement
  project!: Project & { activeLayer: paper.Layer }
  color: string = defaultColor
  strokeWidth: number = defaultBrushSize
  fontSize: number = defaultFontSize
  onResponse = noop
  constructor(canvas: HTMLCanvasElement, width: number, height: number) {
    this.el = canvas
    this.ctx = canvas.getContext('2d') as CanvasRenderingContext2D
    this.project = new Project(canvas)

    // config

    // bind events
    const view = this.project.view
    view.onMouseDrag = e => this.mouseDrag(e)
    view.onMouseDown = e => this.mousedown(e)
    view.onMouseUp = e => this.mouseup(e)
    view.onMouseLeave = e => this.mouseout(e)
    this.setCanvasSize({ width, height })

    const texture = document.createElement('img')
    texture.src = '/img/draw/texture.png'
    texture.onload = () => {
      this.texture = texture
      this.ctx.strokeStyle = this.ctx.createPattern(texture, 'repeat')
    }
  }
  private mousedown(e: paper.MouseEvent) {
    if (e.event.button === RIGHT_CLICK_NUM) return
    switch (this.selectMode) {
      case MODE_BRUSH:
      case MODE_ERASER:
        return this.startDraw(e)
      case MODE_FILL:
        return this.fill(e)
    }
  }
  private mousemove(e: paper.MouseEvent) {
    if (this.selectMode === MODE_BRUSH) {
      if (!this.drawing) return
      const axis = this.setAxis(e)
      this.draw(axis)
    }
    if (this.selectMode === MODE_ERASER) {
      this.erase(e)
    }
  }
  private mouseup(e: paper.MouseEvent) {
    if (e.event.button === RIGHT_CLICK_NUM) return
    if (!this.path || this.selectMode !== MODE_BRUSH) return
    if (this.selectMode === MODE_BRUSH) {
      this.path.simplify(10)
      this.stopDrawing(e)
    }
  }
  private mouseout(e: paper.MouseEvent) {
    this.drawing && this.stopDrawing(e)
  }
  private mouseDrag(e: paper.MouseEvent) {
    const touches = get(e, 'event.targetTouches.length')
    if (touches && touches > 1) return e.stop() // vue-touch event

    this.mousemove(e)
    const moveList = [MODE_SELECT, MODE_ADD_TEXT]
    if (!moveList.includes(this.selectMode)) return
    const layer = this.project.activeLayer

    layer.applyMatrix = true
    layer.translate(e.delta)
    layer.applyMatrix = false
  }
  private startDraw(e: paper.MouseEvent) {
    this.drawing = true
    // this.ctx.beginPath()
    this.pressedAt = Date.now()
    this.currentShape = []
    const axis = this.setAxis(e)
    const path = new Path({
      segments: [new Point(axis.x, axis.y)],
      strokeColor: this.color,
      strokeWidth: this.strokeWidth,
    })
    this.path = path
    this.path.name = 'draw'
  }
  private async stopDrawing(e: paper.MouseEvent) {
    this.drawing = false
    const activeLayer = this.project.activeLayer
    const content = activeLayer.children.content
    const initial = activeLayer.data.type === 'initial'

    //check if its hit
    // const intersect = checkCollision(this.path, this.areaBorder)
    // if (!this.areaBorder || intersect || initial) {
    //   content.addChild(this.path)
    // } else {
    //   const newLayer = this.createLayer()
    //   const content = newLayer.children.content
    //   this.selectLayer(newLayer.id)
    //   this.project.addLayer(newLayer)
    //   content.addChild(this.path)
    //   newLayer.addChild(content)
    //   this.clearShapes()
    // }

    //Cancel check if its hit
    content.addChild(this.path)

    const first = this.path.firstSegment.point
    const last = this.path.lastSegment.point
    const distance = first.getDistance(last, false)
    if (distance < 6) {
      this.path.closePath(false)
      this.path.fillColor = '#ffffff'
    }

    this.updateBorder()
    this.updateShapes()
  }
  @Debounce(1000, { leading: true })
  async updateShapes() {
    console.log('post...')
    this.shapes.push(this.currentShape)
    axios.post('http://api.buzhundong.com/autodraw', this.shapes).then(res => {
      const { data } = res
      const results = data.map((item: any) => {
        return ['url', 'url_variant_1', 'url_variant_2']
          .map((type: string) => ({ name: item.name, confidence: item.confidence, url: item[type] }))
      })
      this.onResponse(union(flatten(results)))
    })
  }
  async colorize() {
    if (this.areaBorder) this.areaBorder.visible = false
    await new Promise(r => setTimeout(r, 50))

    const lineBlob = await new Promise(resolve => this.el.toBlob(blob => resolve(blob as Blob))) as Blob
    const refBlob = await new Promise(resolve => (this.el.cloneNode() as HTMLCanvasElement).toBlob(blob => resolve(blob as Blob))) as Blob
    const form = new FormData()
    form.append('line', lineBlob || '')
    form.append('ref', refBlob || '')
    try {
      const res = await axios.post('http://api.buzhundong.com/autodraw/colorize', form, { timeout: 20000 })
      if (!res) return null
      const url = res.data.url
      return url
    } catch (error) {
      throw error
    }
  }
  async fill(e: paper.MouseEvent) {
    const layer = this.project.activeLayer
    if (!layer) return
    const result = layer.hitTest(e.point)
    if (!result) return
    const type = result.type
    const item = result.item
    if (type === 'fill')
      item.fillColor = this.color
    if (type === 'stroke')
      item.strokeColor = this.color
  }
  private setAxis(e: paper.MouseEvent) {
    this.previousAxis = e.point
    return e.point
  }
  private draw({ x, y }: paper.Point) {
    // this.ctx.lineTo(x, y)
    // this.ctx.lineWidth = 20
    // this.ctx.stroke()
    const position = new Point(x, y)
    this.path.add(position)
    this.currentShape.push({ x, y })
  }
  private erase(e: paper.MouseEvent) {
    const layer = this.project.activeLayer
    this.path.add(e.point)
    this.path.strokeWidth = 15
    this.path.blendMode = 'destination-out'
  }
  async drawSVG(url: string, newLayer?: boolean) {
    const currentLayer = this.project.layers.find(l => l.id === this.project.activeLayer.id) as Layer
    if (newLayer) {
      const layer = this.createLayer()
      const { width, height } = this.project.view.size
      layer.position = new Point(width / 4, height / 4)
      this.selectLayer(layer.id)
    }

    const layer = this.project.activeLayer
    const { left, top } = layer.bounds
    const { width, height } = currentLayer.bounds

    const position = new Point(left + width / 2, top + height / 2)
    const raster = new Raster()
    const min = Math.min(width, height) - (this.areaBorder.visible ? this.borderPadding : 0)
    const size = { width: min, height: min }
    const svg = await new Promise((resolve, reject) => raster.importSVG(url, resolve)) as paper.Group
    svg.set({ bounds: { size } })
    svg.position = position
    svg.strokeColor = this.color
    svg.strokeWidth = this.strokeWidth
    svg.name = 'svg'

    const group = layer.children.content
    group.removeChildren()
    group.addChild(svg)
    this.clearShapes()
  }
  async addImage(el: HTMLImageElement) {
    const layer = this.createLayer()
    const raster = new Raster()
    const { width, height } = this.project.view.size
    raster.width = el.width
    raster.height = el.height
    raster.drawImage(el, new Point(0, 0))
    raster.name = 'image'
    layer.children.content.addChild(raster)
    this.updateBorder()
  }
  //set the area border of the active layer
  async updateBorder() {
    const layer = this.project.activeLayer
    if (this.areaBorder) this.areaBorder.remove()
    if (!layer.children.content.children.length) return

    const { point, size } = layer.bounds
    //data binding
    this.areaBorder = new Group()
    this.areaBorder.name = 'border'

    const padding = this.borderPadding
    const dashed = new Path.Rectangle(point, size)
    dashed.name = 'dashed'
    this.areaBorder.addChild(dashed)
    dashed.strokeColor = 'rgba(0,0,0,0.2)'
    dashed.dashArray = [10, 4]
    dashed.position = new Point(point.x + (size.width - padding) / 2, point.y + (size.height - padding) / 2)
    dashed.bounds.size = new Size(size.width + padding, size.height + padding)
    layer.addChild(this.areaBorder)

    if (!this.areaBorder) return
    if (![MODE_SELECT].includes(this.selectMode)) this.areaBorder.visible = false
    else this.areaBorder.visible = true

    this.updateControlBorder()
  }
  private updateControlBorder() {
    const layer = this.project.activeLayer
    const dashed = (this.areaBorder.children as any).dashed as paper.Path
    const content = layer.children.content
    const padding = this.borderPadding
    // Just keep one
    const _control = (this.areaBorder.children as any).control as Group
    if (_control) _control.remove()

    const offset = DEVICE_MOBILE ? 10 : 5
    const center = content.bounds.center

    const control = new Group()
    control.name = 'control'
    this.areaBorder.addChild(control)
    const setControl = (item: paper.Item) => {
      item.data.type = 'control-point'
      item.strokeColor = dashed.strokeColor
      item.fillColor = 'white'
      item.onMouseDown = () => item.fillColor = dashed.strokeColor
      item.onMouseUp = () => item.fillColor = 'white'
      item.onMouseLeave = () => item.fillColor = 'white'
      control.addChild(item)
    }
    const size = new Size(offset * 2, offset * 2)

    const circle = new Path.Circle(new Point(0, 0), offset)
    setControl(circle)

    const rightBottom = new Path.Rectangle(new Point(0, 0), size)
    setControl(rightBottom)

    const updatePosition = () => {
      const { x, y, width } = content.bounds
      rightBottom.position = dashed.segments[3].point
      circle.position = new Point(x + width / 2, y - 20)
    }
    updatePosition()

    const scale = (e: paper.MouseEvent) => {
      if (this.selectMode !== MODE_SELECT) return
      const center = content.bounds.center
      const { x, y } = e.delta
      const oldDistance = center.getDistance(new Point(e.point.x - e.delta.x, e.point.y - e.delta.y))
      const newDistance = center.getDistance(e.point)
      const scale = newDistance / oldDistance
      content.scale(scale)
      dashed.scale(scale)
      updatePosition()
      e.stop()
    }
    rightBottom.onMouseDrag = scale
    circle.onMouseDrag = e => {
      // const rotation = Math.atan2(e.point.y - layer.bounds.center.y, e.point.x - layer.bounds.center.x) * 180 / Math.PI + 90 - layer.rotation
      // layer.rotate(rotation, center)
      layer.rotation = Math.atan2(e.point.y - layer.bounds.center.y, e.point.x - layer.bounds.center.x) * 180 / Math.PI + 90
      updatePosition()
      e.stop()
    }
  }
  createLayer() {
    const layer = new Layer()
    const group = new Group()
    group.name = 'content'
    layer.addChild(group)
    layer.applyMatrix = false
    layer.name = ` ${this.project.layers.length}`
    layer.sendToBack()
    this.selectLayer(layer.id)
    Object.defineProperty(layer.data, 'type', {
      get() {
        if (layer.children.content.children.length) {
          return undefined
        } else {
          return 'initial'
        }
      }
    })
    return layer
  }
  changeLayerName(name: string, id?: number) {
    // Names consisting of only numbers are not supported
    const newName = name
    if (id) {
      const layer = this.project.layers.find(l => l.id === id)
      if (layer) layer.name = newName
    } else {
      this.project.activeLayer.name = newName
    }
  }
  deleteLayer(id: number) {
    if (this.project.layers.length === 1) return
    const layer = this.project.layers.find(l => l.id === id)
    // ***** CAN'T REMOVE DIRECTLY
    if (layer) {
      layer.removeChildren()
      layer.remove()
      this.updateBorder()
    }
  }
  mergeLayer({ fromId, toId }: { [key: string]: number }) {
    const fromLayer = this.project.layers[fromId]
    const toLayer = this.project.layers.find(l => l.id === toId)
    if (!fromLayer || !toLayer) throw "fromId or toId does not exist."
    this.selectLayer(toLayer.id)
    fromLayer.children.content.copyTo(toLayer.children.content)
    fromLayer.remove()
    this.updateBorder()
  }
  clearBoard() {
    this.project.activeLayer.removeChildren()
  }
  clearShapes() {
    this.currentShape = []
    this.shapes = []
  }
  setCanvasSize({ width, height }: CanvasSize) {
    this.project.view.viewSize.width = width
    this.project.view.viewSize.height = height
  }
  setSelectMode(mode: string) {
    this.selectMode = mode
    this.updateBorder()
  }
  selectLayer(id: number) {
    const layer = this.project.layers.find(l => l.id === id)
    if (!layer) return
    if (layer.id === this.project.activeLayer.id) return
    layer.activate()
    this.updateBorder()
    this.clearShapes()
  }
  getLayerByCoord(x: number, y: number): Layer | null {
    const result = this.project.hitTest(new Point(x, y))
    if (!result) return null
    return result.item.layer
  }
  async copyLayer(id?: number) {
    let layer!: Layer
    if (id) {
      layer = this.project.layers.find(l => l.id === id) as Layer
    } else {
      layer = this.project.activeLayer
    }
    this.areaBorder && this.areaBorder.remove()
    const newLayer = this.createLayer()

    // @ts-ignore (lack .d.ts)
    newLayer.children.content.copyContent(layer.children.content)

    this.project.addLayer(newLayer as Layer)
    this.selectLayer(newLayer.id)
    newLayer.translate(new Point(10, 10))
  }
  addText(text: string, { left, top }: { left: number, top: number }) {
    const textItem = new PointText({
      point: [100, 100],
      content: text,
      fillColor: this.color,
      fontSize: this.fontSize,
      position: new Point(left, top)
    })
    const layer = this.createLayer()
    const group = layer.children.content
    group.addChild(textItem)
    this.selectLayer(layer.id)
    this.updateBorder()
  }
}

function checkCollision(item1: paper.Item, item2: paper.Item): boolean {
  if (!item1 || !item2) return false
  const { left, top, width, height, right, bottom } = item1.bounds
  const i2 = item2.bounds
  if (!inRange(i2.left, left, right) && !inRange(left, i2.left, i2.right)) return false
  if (!inRange(i2.top, top, bottom) && !inRange(top, i2.top, i2.bottom)) return false
  return true
}


