<template>
  <v-toolbar fixed app>
    <v-toolbar-side-icon v-if="DEVICE_MOBILE" @click="$events.emit(EV_TOGGLE_DRAWER)"></v-toolbar-side-icon>
    <v-icon v-else class="logo" color="blue darken-2">data_usage</v-icon>
    <v-toolbar-title v-show="showTitle">SmartDraw</v-toolbar-title>
    <v-spacer></v-spacer>
    <IDrawResults v-if="selectMode === MODE_BRUSH" width="75%" style="padding-top:10px;" />
    <IDrawTextSetting v-if="selectMode === MODE_ADD_TEXT" width="75%" />
  </v-toolbar>
</template>

<script>
  import IDrawResults from '@/components/draw/results.vue'
  import IDrawTextSetting from '@/components/draw/textSetting.vue'
  import { mapState } from 'vuex'
  import { DEVICE_MOBILE, MODE_BRUSH, MODE_ADD_TEXT } from '@/lib/constant'
  export default {
    name: 'IHeader',
    components: { IDrawResults, IDrawTextSetting },
    computed: {
      ...mapState('board', ['selectMode', 'results']),
      showTitle() {
        const mode = this.selectMode
        if ([MODE_BRUSH, MODE_ADD_TEXT].includes(mode)) {
          if (!DEVICE_MOBILE) return true
          if (mode === MODE_BRUSH && !this.results.length) return true
          return false
        }
        return true
      }
    }
  }
</script>
<style lang="less" scoped>
  .logo {
    margin-top: 1px;
  }
</style>



