<template>
  <section>
    <template v-if="!DEVICE_MOBILE">
      <v-btn :color="highlight ? 'primary' : null" icon :style="{color}">
        <v-icon>{{button.icon}}</v-icon>
      </v-btn>
      <small v-text="button.tip"></small>
    </template>
    <template v-else>
      <v-tooltip :disabled="!button.tip" top>
        <v-btn :color="highlight ? 'primary' : null" icon slot="activator" :style="{color}">
          <v-icon>{{button.icon}}</v-icon>
        </v-btn>
        <span v-text="button.tip" />
      </v-tooltip>
    </template>
  </section>
</template>

<script>
  export default {
    name: 'ITipButton',
    props: {
      button: {
        type: Object,
        default: {}
      },
      highlight: Boolean,
      color: String
    }
  }
</script>
<style lang="less" scoped>
  section {
    user-select: none;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
  }
  small {
    white-space: nowrap;
  }
</style>



